<template>
    <div class="padding-t_10" id="reportstabsdata">
        <div class="left-menu-tab new_tab">
            <div class="col-md-12 p-0 ">
                <div class="theme-primary partition">
                    <span class="p-name text-white">{{$t('Ticket')}} {{$t('Reports')}}</span>
                </div>
            </div>
            <div class="pt-2 mb-1 w-100 d-none d-lg-block">
                <div class="input-group customsearchbox">
                    <input type="text" maxlength="50" :placeholder="$t('SearchbyFieldName')" v-model="SearchText" @keyup="SearchTab"  class="form-control searchcustomfilter border-right-0" autocomplete="off">
                    <div class="input-group-append"><span class="input-group-text bg-transparent bg-white border-left-0"><i class="fa fa-search"></i></span></div>
                </div>
            </div>
            <div class="mt-2" id="divscrollsetuptabs" style="max-height:700px">
                <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix lefttabflexible" id="myTab"  role="tablist">
                        <li >
                            <a v-on:click="loadData('TicketSummary');" class="nav-link left-menu" data-toggle="tab" :class="{'active':activeKey==1}" data-bs-toggle="#TicketReport"  href="javascript:void(0)" id="aTicketReport" data-text="TicketDetail" data-ordername="TicketSummary" privilege="TicketDetail">
                                <em class="fa fa-ticket" aria-hidden="true"></em>
                              {{$t('Ticket')}} {{$t('Summary')}} {{$t('Report')}}
                            </a>
                        </li> 
                        <li >
                            <a v-on:click="loadData('TicketPrivilege');" class="nav-link left-menu" data-toggle="tab" :class="{'active':activeKey==2}" data-bs-toggle="#TicketingPrivilegeReport" href="javascript:void(0)" id="aTicketingPrivilegeReport" data-text="TicketingPrivilegeReport" data-ordername="TicketingPrivilegeReport" privilege="TicketingPrivilegeReport">
                                <em class="fa fa-file-text" aria-hidden="true"></em>
                             {{$t('TicketingPrivilege')}} {{$t('Report')}}
                            </a>
                        </li>
                        <li >
                            <a v-on:click="loadData('TicketDetails');" class="nav-link left-menu" data-toggle="tab" :class="{'active':activeKey==3}" data-bs-toggle="#TicketDetailReport" href="javascript:void(0)" id="aTicketDetailReport" data-text="TicketDetailReport" data-ordername="TicketDetailReport" privilege="TicketDetailReport">
                                <em class="fa fa-file-pdf-o" aria-hidden="true"></em>
                               {{$t('Ticket')}} {{$t('Detail')}} {{$t('Report')}}
                            </a>
                        </li>
                    
                </ul>
            </div>
        </div>
        <div class="right-content">
            <div class="col-md-12 p-0">
                 <div class="theme-primary partition p-actions-expand" style="overflow: visible;">
                <span class="p-name text-white" id="spnreporttext">{{ this.headerText }}</span>                
            </div> 
        </div>
        <div class="col-md-12 p-0">
         <div class="tab-content" id="myTabContent">  
           <div class="expand-list">
                   <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                       <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                   </a>
               </div> 
               <div v-if="TicketSummarytab" class="tab-pane pb-2" :class="{ 'active show': activeKey == 1 }" role="tabpanel" id="tab-overlimlit-content" aria-labelledby="tab-overlimlit-content">
                 <div id="listing">                     
                   <div class="col-md-12 p-0">
                   <Form  ref="Expenseform">
                       <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>                       
                   </Form>
                 </div>
               </div>
             </div>
             <div v-if="TicketPrivilegeTab" class="tab-pane pb-2" :class="{ 'active show': activeKey == 2 }" role="tabpanel" id="tab-expensereport-content" aria-labelledby="tab-expensereport-content">
               <div class="listing">
                 <Form  ref="Expenseform">
                   <dynamic-form lang="en"  :buttons="buttons" :schema="FormSchema1" v-on:OnSubmit="onSubmit1"></dynamic-form>                    
                 </Form>
               </div>
             </div>
             <div v-if="TicketDetailsTab" class="tab-pane pb-2" :class="{ 'active show': activeKey == 3 }" role="tabpanel" id="tab-Reimbursements-content" aria-labelledby="tab-Reimbursements-content">
                 <div class="listing">
                   <Form  ref="Expenseform">
                       <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema2" v-on:OnSubmit="onSubmit2"></dynamic-form>                        
                   </Form>
                 </div>
             </div>
             <div class="col-lg-12 float-left p-0 ">
                 <div class="col-md-12  placeholder border mt-0 py-3 w-100 float-left border-top-0">
                     <ReportViewer v-if="parameter.length>0" :parameter="parameter" :reportPath="reportPath"></ReportViewer>                 
                 </div>
             </div>
           </div>
       </div>
    </div>
</div>
</template>
<script>
