                       placeholder: "Select",
                       config:{
                         options: [],
                         // onChange: this.onChangeDropdown
                       },
                       validationRules: {"required": false},
                   },                  
                   {
                        astype: "DateField",
                        label: 'Date From (M/D/YYYY)',
                        name: "TicketDateFrom",
                        value: '',
                        placeholder: "M/D/YYYY",
                        validationRules: {"required": false},
                        config: {
                            format: "M/D/YYYY"
                        }
                    },
                    {
                        astype: "DateField",
                        label: 'Date To (M/D/YYYY)',
                        name: "TicketDateTo",
                        value: '',
                        placeholder: "M/D/YYYY",
                        validationRules: {"required": false},
                        config: {
                            format: "M/D/YYYY"
                        }
                    },
                    {
                       astype: "MultiSelectField",
                       label: "Product",
                       name: "Product",
                       value: "",
                       mode:"tag",
                       placeholder: "Select",
                       config:{
                         options: [],
                         // onChange: this.onChangeDropdown
                       },
                       validationRules: {"required": false},
                   }, 
                   {
                       astype: "MultiSelectField",
                       label: "Channel",
                       name: "Channel",
                       value: "",
                       mode:"tag",
                       placeholder: "Select",
                       config:{
                         options: [],
                         // onChange: this.onChangeDropdown
                       },
                       validationRules: {"required": false},
                   }, 
                   ],
               }
            ],
            FormSchema1: [
               {
                   layoutType: "triple",
                   Data: [
                   {
                       astype: "MultiSelectField",
                       label: "Assign Department",
                       name: "AssignDepartment",
                       value: "",
                       mode:"tag",
                       placeholder: "Select",
                       config:{
                         options: [],
                         // onChange: this.onChangeDropdown
                       },
                       validationRules: {"required": false},
                   }                   
                   ],
               }
            ],
            FormSchema2: [
               {
                   layoutType: "triple",
                   Data: [
                   {
                       astype: "TextField",
                       label: "Ticket Number",
                       name: "TicketNumber",
                       value: "", 
                       validationRules: {"required": false},
                   },
                   {
                     astype: "TextField",
                     label: "Subject",
                     name: "Subject",
                     value: "",  
                     validationRules: {"required": false},
                   },
                   {
                       astype: "MultiSelectField",
                       label: "Submitted By",
                       name: "SubmittedBy",
                       value: "",
                       mode:"tag",
                       placeholder: "Select",
                       config:{
                         options: [],
                         // onChange: this.onChangeDropdown
                       },
                       validationRules: {"required": false},
                   },
                   {
                       astype: "MultiSelectField",
                       label: "Assigned To",
                       name: "AssignedTo",
                       value: "",
                       mode:"tag",
                       placeholder: "Select",
                       config:{
                         options: [],
                         // onChange: this.onChangeDropdown
                       },
                       validationRules: {"required": false},
                   },
                   {
                       astype: "MultiSelectField",
                       label: "Client",
                       name: "Client",
                       value: "",
                       mode:"tag",
                       placeholder: "Select",
                       config:{
                         options: [],
                         // onChange: this.onChangeDropdown
                       },
                       validationRules: {"required": false},
                   },
                   {
                       astype: "MultiSelectField",
                       label: "Priority",
                       name: "Priority",
                       value: "",
                       mode:"tag",
                       placeholder: "Select",
                       config:{
                         options: [],
                         // onChange: this.onChangeDropdown
                       },
                       validationRules: {"required": false},
                   },
                   {
                       astype: "MultiSelectField",
                       label: "AssignDepartment",
                       name: "AssignDepartment",
                       value: "",
                       mode:"tag",
                       placeholder: "Select",
                       config:{
                         options: [],
                         // onChange: this.onChangeDropdown
                       },
                       validationRules: {"required": false},
                   },
                   {
                       astype: "MultiSelectField",
                       label: "Status",
                       name: "Status",
                       value: "",
                       mode:"tag",
                       placeholder: "Select",
                       config:{
                         options: [],
                         // onChange: this.onChangeDropdown
                       },
                       validationRules: {"required": false},
                   },
                   {
                       astype: "MultiSelectField",
                       label: "Category",
                       name: "Category",
                       value: "",
                       mode:"tag",
                       placeholder: "Select",
                       config:{
                         options: [],
                         // onChange: this.onChangeDropdown
                       },
                       validationRules: {"required": false},
                   },                  
                   {
                        astype: "DateField",
                        label: 'Date From (M/D/YYYY)',
                        name: "TicketDateFrom",
                        value: '',
                        placeholder: "M/D/YYYY",
                        validationRules: {"required": false},
                        config: {
                            format: "M/D/YYYY"
                        }
                    },
                    {
                        astype: "DateField",
                        label: 'Date To (M/D/YYYY)',
                        name: "TicketDateTo",
                        value: '',
                        placeholder: "M/D/YYYY",
                        validationRules: {"required": false},
                        config: {
                            format: "M/D/YYYY"
                        }
                    }
                   ],
               }
            ], 
       buttons: [
               {
                   type: "submit",
                   class: "btn btn-success",
                   text: "<i class='fa fa-search pr-2'></i>Search",
               },
               {
                   type: "button",
                   class: "btn btn-danger",
                   text: "<i class='fa fa-close pr-2'></i>Clear All",
                   onClick:this.onClear
               }
       ],
            UserLicenseModules: null,
            ConfgurationValueByKey: null,
            SearchText:'',
            activeKey:1,
            isLoading:false,
            reportPath:null,
            parameter:[],
            TicketSummarytab:true,
            TicketPrivilegeTab:false,
            TicketDetailsTab:false,
            headerText:'Ticket Summary Report',
                
        };
    },
    created: function () {
        // this.GetUserLicenseModulesReport();
        this.GetUserList();
        this.GetClientList();
        this.GetPriorityList();
        this.GetAllDepartmentList();
        this.GetTicketStatus();
        this.GetCategory();
        this.GetProductsList();
        this.GetChannelList();
    },    
    methods: {
        SearchTab:function(event){            
       var val = this.SearchText;
       $("ul#myTab").find("li").each(function () {
           val = val.toLowerCase();
           var text = $(this).find("a").text().toLowerCase();
           if (text.includes(val)) {
               $(this).closest("li").show();
           }
           else {
               $(this).closest("li").hide();
           }
       });
   },
        GetUserList:function(){
            var vm=this;
            var url=`moduleName=TicketReport`;
            DataService.GetUserList(url).then(response => {
                if(response.data.length>0){
                    var SubmittedByData=response.data;                    
                    SubmittedByData.forEach(item=>{
                        vm.FormSchema[0].Data[2].config.options.push({ name:item.userName,value:item.userId});
                        vm.FormSchema[0].Data[3].config.options.push({ name:item.userName,value:item.userId});
                        vm.FormSchema2[0].Data[2].config.options.push({ name:item.userName,value:item.userId});
                        vm.FormSchema2[0].Data[3].config.options.push({ name:item.userName,value:item.userId});
                    })
                }
            })
        },
        GetClientList:function(){
            var vm=this;
            var url=`reqFrom=CUSTOM`;
            DataService.GetClientList(url).then(response => {
                if(response.data.status=="Success"){
                    var SubmittedByData=response.data.result;
                    SubmittedByData.forEach(item=>{
                        vm.FormSchema[0].Data[4].config.options.push({ name:item.PARENT_NAME,value:item.PARENT_ID});
                        vm.FormSchema2[0].Data[4].config.options.push({ name:item.PARENT_NAME,value:item.PARENT_ID});
                    })
                }
            })
        },
        GetPriorityList:function(){          
            var vm=this;
            var url=`isSupportTicket=false`;
            DataService.GetPrioritybyCompanyId(url).then(response => {             
                if(response.data.length>0){
                    var SubmittedByData=response.data;
                    SubmittedByData.forEach(item=>{                        
                        vm.FormSchema[0].Data[5].config.options.push({ name:item.prioritY_NAME,value:item.prioritY_ID})
                        vm.FormSchema2[0].Data[5].config.options.push({ name:item.prioritY_NAME,value:item.prioritY_ID});
                    })
                }
            })
        },
        GetAllDepartmentList:function(){                      
            var vm=this;          
            DataService.GetDepartmentListbyCompanyForClient().then(response => {                             
                if(response.data.length>0){
                    var SubmittedByData=response.data;                    
                    SubmittedByData.forEach(item=>{                        
                        vm.FormSchema[0].Data[6].config.options.push({ name:item.departmenT_NAME,value:item.departmenT_ID});
                        vm.FormSchema1[0].Data[0].config.options.push({ name:item.departmenT_NAME,value:item.departmenT_ID});
                        vm.FormSchema2[0].Data[6].config.options.push({ name:item.departmenT_NAME,value:item.departmenT_ID})
                    })
                }
            })
        },
        GetTicketStatus:function(){            
            var vm=this;           
            DataService.GetTicketStatus().then(response => {
                if(response.data.status=="Success"){
                    var SubmittedByData=response.data.result;                                        
                    SubmittedByData.forEach(item=>{
                        vm.FormSchema[0].Data[7].config.options.push({ name:item.STATUS_NAME,value:item.STATUS_ID});
                        vm.FormSchema2[0].Data[7].config.options.push({ name:item.STATUS_NAME,value:item.STATUS_ID})
                    })
                }
            })
        },
        GetCategory:function(){  
            var vm=this;   
            var url=`categoryTypeId=TICKET_CATEGORY&is_Global=false&showActiveStatus=true`;       
            DataService.GetDDLCategoryForTicket(url).then(response => { 
                if(response.data.length>0){
                    var SubmittedByData=response.data;                    
                    SubmittedByData.forEach(item=>{                        
                        vm.FormSchema[0].Data[8].config.options.push({ name:item.name,value:item.value});
                        vm.FormSchema2[0].Data[8].config.options.push({ name:item.name,value:item.value});
                    })
                }
            })
        },
        GetProductsList:function(){
            var vm=this;   
            DataService.GetProductsList().then(response => { 
                if(response.data.length>0){
                    var SubmittedByData=response.data;
                    SubmittedByData.forEach(item=>{                        
                        vm.FormSchema[0].Data[11].config.options.push({ name:item.name,value:item.value})
                    })
                }
            })
        },
        GetChannelList:function(){            
            var vm=this;   
            DataService.GetDDLChannelByCompanyId().then(response => { 
                if(response.data.length>0){                    
                    var SubmittedByData=response.data;
                    SubmittedByData.forEach(item=>{                        
                        vm.FormSchema[0].Data[12].config.options.push({ name:item.name,value:item.value})
                    })
                }
            })
        },
        loadData:function(tabName){            
           var vm=this;
           if(tabName=='TicketSummary'){              
               vm.activeKey=1;
               vm.TicketSummarytab=true;
               vm.TicketPrivilegeTab=false;
               vm.TicketDetailsTab=false;
               vm.headerText='Ticket Summary Report';
               vm.parameter=[]
               
           }
           else if(tabName=='TicketPrivilege'){
               vm.activeKey=2;
               vm.TicketSummarytab=false;
               vm.TicketPrivilegeTab=true;
               vm.TicketDetailsTab=false;
               vm.headerText='Ticket Privilege Report';
               vm.parameter=[];
              
           }
           else{              
               vm.activeKey=3;
               vm.TicketSummarytab=false;
               vm.TicketPrivilegeTab=false;
               vm.TicketDetailsTab=true;
               vm.headerText='Ticket Details Report';
               vm.parameter=[];
              
           }
        },
        onClear:function(){
       var vm=this;
       if(vm.activeKey==1){
         (vm.FormSchema)[0].Data[0].value=[];
         (vm.FormSchema)[0].Data[1].value=[];
         (vm.FormSchema)[0].Data[2].value=[];
         (vm.FormSchema)[0].Data[3].value=[];
         (vm.FormSchema)[0].Data[4].value=[];
         (vm.FormSchema)[0].Data[5].value=[];
         (vm.FormSchema)[0].Data[6].value='';
         (vm.FormSchema)[0].Data[7].value='';
         (vm.FormSchema)[0].Data[8].value='';
         (vm.FormSchema)[0].Data[9].value='';
         (vm.FormSchema)[0].Data[10].value='';
         (vm.FormSchema)[0].Data[11].value='';
         (vm.FormSchema)[0].Data[12].value='';
         vm.parameter=[];
       }
       if(vm.activeKey==2){
         (vm.FormSchema1)[0].Data[0].value=[];         
         vm.parameter=[];
       }
       if(vm.activeKey==3){          
         (vm.FormSchema2)[0].Data[0].value=[];
         (vm.FormSchema2)[0].Data[1].value=[];
         (vm.FormSchema2)[0].Data[2].value=[];
         (vm.FormSchema2)[0].Data[3].value=[];
         (vm.FormSchema2)[0].Data[4].value=[];
         (vm.FormSchema2)[0].Data[5].value=[];
         (vm.FormSchema2)[0].Data[6].value='';
         (vm.FormSchema2)[0].Data[7].value='';
         (vm.FormSchema2)[0].Data[8].value='';
         (vm.FormSchema2)[0].Data[9].value='';
         (vm.FormSchema2)[0].Data[10].value='';        
         vm.parameter=[];
       }        
        },
        onSubmit:function(e) {
            debugger
          var vm=this;
          var SubmittedBys='';
          var AssignedTos='';
          var Clients='';
          var Prioritys='';  
          var Categorys='';        
          var AssignDepartments='';
          var Statuss='';
          var Products='';
          var Channels='';
          e.SubmittedBy.forEach((item) => {
            SubmittedBys+=item.value+','
          });
          e.AssignedTo.forEach((item) => {
            AssignedTos+=item.value+','
          });
          e.Client.forEach((item) => {
            Clients+=item.value+','
          });
          e.Priority.forEach((item) => {
            Prioritys+=item.value+','
          });
          e.AssignDepartment.forEach((item) => {
            AssignDepartments+=item.value+','
          });
          e.Status.forEach((item) => {
            Statuss+=item.value+','
          });
          e.Category.forEach((item) => {
            Categorys+=item.value+','
          });
          e.Product.forEach((item) => {
            Products+=item.value+','
          });
          e.Channel.forEach((item) => {
            Channels+=item.value+','
          });
           var reportModel={
               reportName:'ticketdetailreport',
               moduleName:'Ticketing',
               TicketNumber:e.TicketNumber==''?null:e.TicketNumber,
               TicketSubject:e.Subject==''?null:e.Subject,
               SubmittedBy:SubmittedBys==''?null:SubmittedBys,
               AssignedTo:AssignedTos==''?null:AssignedTos,
               Client:Clients==''?null:Clients,
               Priority:Prioritys==''?null:Prioritys,
               AssignDepartment:AssignDepartments==''?null:AssignDepartments,
               Status:Statuss==''?null:Statuss,
               Category:Categorys==''?null:Categorys,
               Product:Products==''?null:Products,
               Channel:Channels==''?null:Channels,
               dateFrom: vm.$options.filters.formatDatee(e.TicketDateFrom),
               dateTo:vm.$options.filters.formatDatee(e.TicketDateTo)
           }  
           var objectPostString = JSON.stringify(reportModel);     
           DataService.ReportMain(objectPostString).then(response => {  
               vm.reportPath=response.data.reportpath;
               vm.parameter=response.data.parameters;
           });
        },
        onSubmit1:function(e) {
            debugger
            var vm=this;
            var AssignDepartments='';
            e.AssignDepartment.forEach((item) => {
                AssignDepartments+=item.value+','
            });
            var reportModel={
               reportName:'tickethodreport',
               moduleName:'Ticketing',              
               AssignDepartment:AssignDepartments==''?null:AssignDepartments                         
           }  
            var objectPostString = JSON.stringify(reportModel);     
           DataService.ReportMain(objectPostString).then(response => {                                                          
               vm.reportPath=response.data.reportpath;
               vm.parameter=response.data.parameters;
           });
        },
        onSubmit2:function(e) {            
            var vm=this;
          var SubmittedBys='';
          var AssignedTos='';
          var Clients='';
          var Prioritys='';  
          var Categorys='';        
          var AssignDepartments='';
          var Statuss=''; 
          e.SubmittedBy.forEach((item) => {
            SubmittedBys+=item.value+','
          });
          e.AssignedTo.forEach((item) => {
            AssignedTos+=item.value+','
          });
          e.Client.forEach((item) => {
            Clients+=item.value+','
          });
          e.Priority.forEach((item) => {
            Prioritys+=item.value+','
          });
          e.AssignDepartment.forEach((item) => {
            AssignDepartments+=item.value+','
          });
          e.Status.forEach((item) => {
            Statuss+=item.value+','
          });
          e.Category.forEach((item) => {
            Categorys+=item.value+','
          });        
          
           var reportModel={
               reportName:'ticketreport',
               moduleName:'Ticketing',
               TicketNumber:e.TicketNumber==''?null:e.TicketNumber,
               TicketSubject:e.Subject==''?null:e.Subject,
               SubmittedBy:SubmittedBys==''?null:SubmittedBys,
               AssignedTo:AssignedTos==''?null:AssignedTos,
               Client:Clients==''?null:Clients,
               Priority:Prioritys==''?null:Prioritys,
               AssignDepartment:AssignDepartments==''?null:AssignDepartments,
               Status:Statuss==''?null:Statuss,
               Category:Categorys==''?null:Categorys,               
               dateFrom:vm.$options.filters.formatDatee(e.TicketDateFrom),
               dateTo:vm.$options.filters.formatDatee(e.TicketDateTo)           
           }  
           var objectPostString = JSON.stringify(reportModel);     
           DataService.ReportMain(objectPostString).then(response => {                                                          
               vm.reportPath=response.data.reportpath;
               vm.parameter=response.data.parameters;
           });
